body.dark-mode {
  transition: all 0.3s ease-in-out;
  background-color: #212121;
  filter: invert(1) hue-rotate(180deg);
}
body.dark-mode img {
  filter: invert(1) hue-rotate(180deg);
}
body.dark-mode .main-navigation img {
  filter: unset;
}
body.dark-mode .iframe-gradient svg {
  filter: invert(1) hue-rotate(180deg);
}
body.dark-mode .iframe-gradient::before {
  filter: invert(1) hue-rotate(180deg);
}

.container {
  max-width: 1290px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}